:root {
    /* Color Palette */
    --color-primary: #c5a46d;
    --color-secondary: #25384d;
    --color-background: #f8f4f0;
    --color-surface: #ffffff;
    --color-text-primary: #2a2a2a;
    --color-text-secondary: #666666;
    --color-white: #ffffff;
    --color-black: #000000;
    --color-border-light: #f5f5f5;
    --color-icon-bg: #c8bdaa;

    /* Typography */
    --font-primary: var(--font-playfair-display), serif;
    --font-secondary: var(--font-poppins), sans-serif;

    /* Font Sizes */
    --font-size-h1: 3.052rem; /* 48.83px */
    --font-size-h2: 2.441rem; /* 39.06px */
    --font-size-h3: 1.953rem; /* 31.25px */
    --font-size-h4: 1.563rem; /* 25.00px */
    --font-size-h5: 1.25rem;  /* 20.00px */
    --font-size-base: 1rem;    /* 16.00px */
    --font-size-sm: 0.8rem;   /* 12.80px */
    --font-size-xs: 0.64rem;  /* 10.24px */

    /* Spacing */
    --spacing-xs: 4px;
    --spacing-sm: 8px;
    --spacing-md: 16px;
    --spacing-lg: 32px;
    --spacing-xl: 64px;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
  font-family: var(--font-secondary);
  color: var(--color-text-primary);
  background-color: var(--color-background);
}

h1, h2, h3, h4, h5 {
    font-family: var(--font-primary);
    font-weight: 700;
    color: var(--color-text-primary);
}

h1 { font-size: var(--font-size-h1); }
h2 { font-size: var(--font-size-h2); }
h3 { font-size: var(--font-size-h3); }
h4 { font-size: var(--font-size-h4); }
h5 { font-size: var(--font-size-h5); }

p {
    font-size: var(--font-size-base);
    line-height: 1.6;
    color: var(--color-text-secondary);
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.highlight {
  color: var(--color-primary);
}

.whatsapp-float {
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 100;
  width: 50px;
  height: 50px;
  transition: transform 0.3s ease;
}

.whatsapp-float:hover {
  transform: scale(1.1);
}

.whatsapp-float img {
  width: 100%;
  height: 100%;
}

@media (max-width: 768px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
  .whatsapp-float {
    width: 40px;
    height: 40px;
    bottom: 20px;
    right: 20px;
  }
}

